<template>
	<div style="margin: 100px 150px 0 0">
		<div id="container"></div>
	</div>
</template>

<script>
	import { Pie } from '@antv/g2plot';
	export default {
		data() {
			return {
				dataSource: [],
			};
		},
		methods: {
			async getUserOccupationData() {
				let {
					data
				} = await this.$get(this.$store.state.requestPath + '/admin/user_occupation/chart');
				this.dataSource = data.data;
				console.log(data.data)
			},
		},
		async created() {
			this.getUserOccupationData();
		},
		watch: {
			dataSource: async function(data) {
				const piePlot = new Pie('container', {
				  appendPadding: 10,
				  data,
				  angleField: 'value',
				  colorField: 'type',
				  radius: 1,
				  innerRadius: 0.64,
				  meta: {
				    value: {
				      formatter: (v) => `${v}`,
				    },
				  },
				  label: {
				    type: 'inner',
				    offset: '-50%',
				    autoRotate: false,
				    style: { textAlign: 'center' },
				    formatter: ({ percent }) => `${(percent * 100).toFixed(0)}%`,
				  },
				  statistic: {
				    title: {
				      offsetY: -20,
				    },
				    content: {
				      offsetY: -4,
				    },
				  },
				  // 添加 中心统计文本 交互
				  interactions: [
				    { type: 'element-selected' },
				    { type: 'element-active' },
				    {
				      type: 'pie-statistic-active',
				      cfg: {
				        start: [
				          { trigger: 'element:mouseenter', action: 'pie-statistic:change' },
				          { trigger: 'legend-item:mouseenter', action: 'pie-statistic:change' },
				        ],
				        end: [
				          { trigger: 'element:mouseleave', action: 'pie-statistic:reset' },
				          { trigger: 'legend-item:mouseleave', action: 'pie-statistic:reset' },
				        ],
				      },
				    },
				  ],
				});
				piePlot.render();
			}
		},

	}
</script>

